body,
html {
  margin: 0;
  padding: 0;
  height: 100%;
}
ul li {
  list-style-type: none;
}

.app-wrapper {
  display: grid;
  grid-template-rows: 60px 1 fr;
  grid-template-columns: 2fr 10fr;
  grid-template-areas:
    "header header"
    "nav content";
  grid-gap: 10px;
  margin: 0 auto;
  height: 100vh;
}
.app-wrapper-content {
  grid-area: content;
  height: 90vh;
}
